@import './discovery-intro';
@import './discovery-steps';
@import './discovery-lens';
@import './discovery-lens-info';
@import './discovery-arrows';
@import './discovery-splashscreen';

.discovery {
    position: relative;    
    @extend .flex-center;

    &__navbar {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 12.5rem;
        opacity: 1;
        background: $background-darker;
        z-index: 2;
        transition: all 1s ease-in-out;
        pointer-events: none;

        &--hide {
            height: 22rem;
            opacity: 0;
        }
    }

    &__montage {
        object-fit: cover;
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1;
        // transition: all .5s ease-in-out;
    }
    
    &__end-experience {
        position: absolute;
        bottom: 5rem;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        opacity: 0;
        transition: opacity .5s ease-in-out;

        &--fadein {
            opacity: 1;
        }
    }

    &__final-frame {
        position: fixed;
        top:0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url('../../../assets/images/discovery/04_plan_logo2-min.png');        
        background-size: cover;
        background-position: center;
        z-index: 1;
        opacity: 0;
        transition: all .5s ease-in;

        &--fadein {
            opacity: 1;
        }
    }

    &__textture {
        opacity: 0;
        background-image: url('./../../../assets/images/discovery/texture/loop_verre01_plan01_00000-min.png');
        background-size: cover;
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1;
        pointer-events: none;
        background-position: center;
        animation: animateTexttureImage 2.5s  infinite;
        transition: opacity .5s ease-in;

        &--fadein {
            opacity: 1;
        }
    }
}


