.discovery-lens{
    position: relative;
    width: 100%;
    height: 100%;
    // background: rgba(#fff,.1);
    z-index: 1;

    &__lens-wrapper {
        width: 100%;
        height: 100%;
        transform: translateX(0);
        z-index: 4;
        position: relative;
        transition: all .75s ease-in-out;

        &--correct {
            transform: translateX(13rem);
        }

        &--protect {
            transform: translateX(-22rem);
        }

        &--enhance {
            transform: translateX(-57rem)
        }
                
    }

    &__sprite {
        position: absolute;
        z-index: 1;
        opacity: 0;
        transition: opacity 1s ease-in-out;

        &--show {
            opacity: 1;
        }

        &-arrow {
            position: absolute;
            top: 0;
            bottom: 0;
            color: red;
            margin: auto;
            display: flex;
            align-items: center;
            justify-content: center;
            left: -4rem;

            &--right{
                right: -4rem;
                left: initial;
            }
        }
    }

    &__background {
        position: fixed;
        top:0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url('../../../assets/images/discovery/04_background_plan_logo2-min.png');        
        background-size: cover;
        background-position: center;
        z-index: 1;
        opacity: 0;
        transition: all 1s ease-in;

        &--fadein {
            opacity: 1;
        }
    }

    &__lens {
        @extend .flex-center;
        flex-flow: column;
        z-index: 2;

        label {
            font-size: 1.1rem;
            font-weight: bold;
            border: 1px solid rgba(#fff, .1);
            border-radius: 1.1rem;
            height: 2.2rem;
            width: 7.5rem;
            color: #fff;
            transform:translateY(5rem);
            display: inline-flex;
            @extend .flex-center;
            display: none;
        }

        &--protect {       
            position: absolute;
            top: calc(50% - 25px);
            left: calc(50% - -29px);
            transform: translate(-50%, -50%);

            img{
                transform: translate(-2.4rem, -2rem) scale(1.25);
            } 
            .discovery-lens__sprite{
                background: url('./../../../assets/images/discovery/sprites/PROTECT_loop_sprite.png') no-repeat -11px -26px;
                background-size: 1750px 2726px;
                width: 157px;
                height: 177px;
                transform: translate(-2.4rem, -2rem) scale(1.35);

                animation: animateProtect 3.5s  infinite;
                animation-timing-function: step-end;  
            }    
        }

        &--enhance {
            position: absolute;
            top: calc(50% - 25px);
            left: calc(50% - -368px);
            transform: translate(-50%, -50%);

            img{
                transform: translate(-2.3rem, -1.7rem) scale(1.32);
            }
            .discovery-lens__sprite{
                background: url('./../../../assets/images/discovery/sprites/ENHANCE_loop_sprite.png') no-repeat -6px -26px;
                background-size: 1750px 2726px;
                width: 168px;
                height: 177px;
                transform: translate(-2.2rem, -1.6rem) scale(1.34);

                animation: animateEnhance 3.5s  infinite;
                animation-timing-function: step-end;    
            }
        }

        &--correct {
            position: absolute;
            top: calc(50% - 25px);
            left: calc(50% - 310px);
            transform: translate(-50%, -50%);
            
            img{
                transform: translate(-2.6rem, -1.8rem) scale(1.18);
            }

            .discovery-lens__sprite {
                background: url('./../../../assets/images/discovery/sprites/CORRECT_loop_sprite.png') no-repeat -838px -2509px;
                background-size: 1700px 2726px;
                width: 149px;
                height: 177px;  
                transform: translate(-2.4rem, -1.5rem) scale(1.32);  
                animation: animateCorrect 3.5s  infinite;
                animation-timing-function: step-end;    
            }    
        }
    }

    &__lens-outline{
        width: 17rem;
        opacity: 0;
        transition: all 1s ease-in;

        &--fadein {
            opacity: 1;
        }
    }

    &__labels {
        position: absolute;
        top: calc(50% + 12rem);
        left: 50%;
        color: #fff;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        transform: translate(-50%, -50%);

        li{
            font-size: 1.1rem;
            font-weight: bold;
            border: 1px solid rgba(#fff, .1);
            border-radius: 1.1rem;
            height: 2.2rem;
            width: 7.5rem;
            color: #fff;
            display: inline-flex;
            @extend .flex-center;
            transition: all 1s ease-in-out;
            text-transform: capitalize;
            opacity: .5;

            &:first-child {
                transform: translateX(-26rem)
            }

            &:last-child {
                transform: translateX(27rem)
            }

            &.active {
                opacity: 1;
            }
        }

        &--correct {
            li{
                border: none;
                
                &:first-child{
                    background-color: #fff;
                    color: $theme;

                    + li {
                        transform: translateX(-23.5rem);
                    }
                }
                

                &:last-child {
                    transform: translateX(-23rem);
                }
            }
        }

        &--protect {
            li {
                border: none;
                &:first-child{
                    transform: translateX(-2rem);
                    + li {
                        background-color: #fff;
                        color: $theme;
                    }
                }

                &:last-child{
                    transform: translateX(2.5rem);
                    border: none;
                }
            }
        }

        &--enhance {
            li{
                border: none;

                &:first-child,&:first-child + li {
                    transform: translateX(25rem);
                }

                &:last-child{
                    background-color: #fff;
                    color: $theme;
                }
            }
        }
    }
}

