.prescription-input {
   display: inline-flex;
   align-items: center;
   position: relative;
   border: 1px solid $border-color-light-blue;
   border-radius: .8rem;
   height: 5rem;

   // display: inline-flex;
   // align-items: center;
   // justify-content: center;
   width: calc(50% - 1rem);
   

   //   height: 5rem;
   //   font-size: 1.75rem;
   //   overflow: hidden;

     &__label {
      @extend .flex-center;
      width: 8rem;
      background: rgba($theme, 0.15);
      font-size: 1.8rem;
      height: 100%;
      text-transform: uppercase;
     }

     &__value {
      width: 16.5rem;
      border: none;
      font-size: 1.75rem;
      color: $text-color-main;
      text-align: center;
      outline: none;
      padding: 0 1rem;      
      flex: 1;

      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
         -webkit-appearance: none;
         margin: 0;
      }
   }

     &__error {
         position: absolute;
         bottom: -2rem;
         right: .5rem;
         font-size: 1.25rem;
         color: #ff7c7c;
     }
}



