.prescription-side {

    &__title{
        display: flex;
        align-items: center;
        color: $theme;
        font-size: 1.2rem;
        font-weight: bold;
        z-index: 2;


        img{
            margin-right: 1rem;
        }
    }

    &__image {
        margin: auto;
        width: 80%;
    }

    &__clear-lens {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
        overflow: hidden;
        border-top-right-radius: 1rem;
        border-bottom-right-radius: 1rem;
        overflow: hidden;    
    }

    &__precision {
        position: relative;
        height: 100%;
        width: 100%;
        overflow: hidden;

        img {
            width: 100%;
            position: absolute;
            @extend .position-center-absolute;
            transition: all .75s ease-in-out;
            
            &:first-child {
                left: calc(50% + 20rem);
                transform: translate(-50%, -50%) scale(.6);
                opacity: 0;
            }
        }

        &--swap {
            img {
                &:first-child {
                    left: calc(50%);
                    transform: translate(-50%, -50%) scale(1);
                    opacity: 1;
                }

                &:last-child{
                    left: calc(50% - 20rem);
                    transform: translate(-50%, -50%) scale(.6);
                    opacity: 0;
                }
            }
        }
    }


    &__buttons {
        display: flex;
        margin-left: auto;
        z-index: 2;

        .button {
            margin-left: 1rem;
        }
    }

    &__import-modal {
        height: 100%;
        display: flex;
        align-items: flex-start;
        flex-flow: column;
        padding: 5rem 4.5rem;

        div {
            display: flex;
            align-items: center;
            justify-content: center;
            margin: auto auto 0;

            .button {
                margin: 0 .5rem;
            }
        }
    }
}