.need-explorer-question-panel {
    @extend .box-shadow-light-blue;
    padding: 4rem 5.5rem;
    border-radius: 15px;
    position: relative;

    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-flow: column;

    &__step {
        font-size: 1.2rem;
        color: $theme;
        font-weight: bold;

        display: flex;
        align-items: center;

        img{
            margin-right: 2rem;
        }
    }

    &__next {
        position: absolute;
        right: 3rem;
        bottom: 3rem;
    }
}

.need-explorer-question-panel--kids{
    background-color: var(--app-theme-main);
    color: var(--app-theme-main);
    box-shadow: 0px 5px 20px -4px;

    .need-explorer-question-panel{
        &__step{
            color: #fff;
        }
    }

    .need-explorer-question__question{
        color: #fff;
        font-size: 2.5rem;
    }
}