.modal{
    overflow: hidden;
    display: flex;
    flex-flow: column;
    max-height: calc(100vh - 3rem);

    &-overlay{
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        background: rgba(0, 0, 0, 0.5);
        z-index: 9000;
    }

    @extend .position-center-fixed;
    background: #fff;
    padding: 1.75rem;
    z-index: 9001;
    border-radius: 1.5rem;
    //default
    width:54.5rem;
    max-width: calc(100vw - 5rem);

    &--transparent {
        background-color: transparent;
    }

    &--no-padding {
        padding: 0;
    }

    &__close {
        position: absolute;
        top: 1.25rem;
        right: 1.25rem;
        z-index: 2;
    }

    &__content {
        height: 100%;
        flex: 1;
    }
}