.lens-consultation-side-v2 {
    height: 100%;

    &__top {
        position: relative;
        z-index: 3;

        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

        label {
            font-weight: 700;
            font-size: 1.3rem;
            color: $theme;
            display: flex;
            align-items: center;

            img{
                margin-right: 1rem;
            }
        }
    }

    &__lens-pairs{
        display: flex;
        align-items: center;

        img{
            filter: brightness(0) invert(1);
            transition: opacity .25s ease-in-out;
        }
        span{
            width: 1px;
            height: 1.5rem;
            background: #eee;
            margin: 0 2rem;
            display: inline-block;
            display: flex;
            align-items: center;
        }

        &--pair1{
            img:last-child{
                opacity: .5;
            }
        }
        &--pair2{
            img:first-child{
                opacity: .5;
            }
        }
    }




    &__bottom {
        position: relative;
        z-index: 3;
        width: 100%;
        display: flex;
        justify-content: flex-end;

        .button {
            margin-left: 1rem;
        }
    }

    &__eye-blink{
        width: 2.5rem;
        margin-left: 1rem;
        position: relative;

        img{
            width: 100%;
            transition: transform .25s ease-in-out;
            filter: brightness(0) invert(1);

            &:first-child{
                position: absolute;
            }
        }

        .svg{
            opacity: 0;
        }
    }
}