.license-activation {
    height: 100vh;
    width: 100%;
    background-color: #ddd;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(./../../../assets/images/discovery/license-activation-bg-min.png);
    background-size: cover;
    background-position: top center;



    &__form{
        background-color: #fff;
        padding: 5rem;
        border-radius: 1.5rem;
        width: 48rem;

        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-90%, -50%);
    }


    &__logo {
        width: 5rem;
        height: 5rem;
    }

    &__title{
        margin: 2rem 0 4rem;
    }

    &__connect {
        margin-left: auto;
    }

    &__fields {
        margin-bottom: 2rem;

        input {
            width: 100%;
            margin-bottom: 1rem;
        }
    }
}