.sub-products {

    width: 100%;
    height: 100%;
    background-color: #fff;
    transition: top .5s ease-out;

 

    &__list {
        li {
            height: 5rem;
            border-bottom: 1px solid $border-color-light;
            padding: 0 2rem;

            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 1.2rem;

            img{
                cursor: pointer;
            }
        }
    }
}