.product-demo-compare {
    width: 100%;
    height: 100%;

    &__back {
        position: absolute;
        top: 3rem;
        left: 3.5rem;
        z-index: 7;
    }

    &__product-selections {
        position: absolute;
        width: 100%;
        bottom: 0;
        // height: 30rem;
        display: flex;

        > div:first-child{
            transition: all .20s ease;
        }
    }
    &__splitter {
        position: absolute;
        left: 50%;
        top: 0;
        margin: auto;

        width: .3rem;
        height: 100%;
        background: #fff;
        z-index: 6;
        transition: all .20s ease;

        span {
            width: 1rem;
            height: 1rem;
            background: rgba(#fff,1);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(45deg);
            z-index: 6;
        }
    }

    &__slider {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 3rem;
        margin: auto;
        z-index: 6;
        opacity: 0;
    }
}