.delivery-vision-test {
    width: 100%;
    max-width: 86rem;
    margin-left: auto;
    margin-right: auto;

    h2 {
        margin: 4rem 0;
    }

    ul{
        display: flex;
        align-items: center;
        justify-content: center;

        li{
            width: 27.1rem;
            height: 35.3rem;
            border-radius: .8rem;
            border: 2px solid rgba($theme, .2);            
            display: flex;
            align-items: center;
            justify-content: center;
            flex-flow: column;
            color: $theme;

            div {
                flex: 1;
                @extend .flex-center;
            }

            img {
                width: 8.8rem;
            }

            h5{
                margin-bottom: 6rem;
            }

            &:not(:last-child) {
                margin-right: 2rem;
            }

            &:first-child {
                + li {
                    img {
                        width: 7.2rem;
                    }
                }
            }

            &:last-child {
                img {
                    width: 4.5rem;
                }
            }
        }
    }
}