.product-selection {
    flex: 1;
    padding: 4rem 3rem;
    opacity: 0;
    transition: opacity .25s ease-in;

    &--fade-in{
        opacity: 1;
    }

    &__scroll {
        overflow-x: auto;
        padding-bottom: 4rem;
        transform: translate(0rem, -1rem);
        height: 100%;
    }
    
    &__list {
        display: flex;
        height: 100%;
        padding-left: 1rem;
        height: 100%;
    }
}