.need-explorer-category {

    &__active-animation{
        position: fixed;
        background-color: $theme;
        height: 3rem;
        border-radius: 1.5rem;
        transition: all .35s ease-in-out;
    }

    &__items {
        position: relative;
        z-index: 1;
    }

    &__item {
        @extend .flex-center;
        margin-bottom: 2rem;

        &-label {
            height: 3rem;
            border-radius: 1.5rem;
            // background: #eee;
            @extend .flex-center;
            font-size: 1.5rem;
            padding: 0 3rem;
            color: $text-color-disabled;
            transition: color .5s ease-in-out;

            &--completed {
                color: $text-color-main;
            }

            &--active {
                // background: $theme;
                color: #fff;
            }
        }
        
    }
    

    &--kids {
        .need-explorer-category {
            &__active-animation{
                @include kidsBackground();
            }
        }
    }
}