.emat{
    height: $pageHeight;

    .emat-container{
        height: 100%;
        padding: 2rem 3.5rem;
        display: grid;
        grid-template-rows: 1fr 6rem;
        grid-template-columns: 20rem 1fr;
        grid-gap: 0 3rem;

        .file-sort{
            padding-top: 4rem;
        
            &__item {
                @extend .flex-center;
                margin-bottom: 2rem;
        
                label {
                    height: 3rem;
                    border-radius: 15px;
                    @extend .flex-center;
                    font-size: 1.5rem;
                    padding: 0 3rem;
                    color: $text-color-light;
                }
        
                &--active {
                    label {
                        background: $theme;
                        color: #fff;
                    }
                }
            }
        }
    
        .file-container{
            padding: 4rem 5.5rem;
            border-radius: 15px;
            min-height: 100%;
        
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            flex-flow: column;
            
            div label{
                font-size: 1.5rem;
                color: $theme;
            }
        
            &__items{
                width: 100%;
                overflow-y: auto;
            }

            &__item{
                cursor: pointer;
                display: flex;
                padding: 2rem 0rem;
                border-bottom: 1px solid $border-color;
                font-size: 1.5rem;
                color: $text-color-main;

                .icon-file{
                    height: 2rem;
                }

                .file-name{
                    flex: 1 1 0;
                    margin-left: 2rem;
                }

                .date-created{
                    flex: 1 1 0;
                    color: $text-color-light;
                }

                .icon-view{
                    height: 2rem;
                    margin-right: 2rem;
                }
            }
        }
    }
}