.product-demo {
    position: absolute;
    top: 0;
    left:0;
    width: 100%;
    height: 100%;
    z-index: 2;

    &__video {
        width: 100%;    
        height: 100%;        
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
    }

    &__drive {
        width: 100%;
        height: 100%;
        background-size: cover;
        position: absolute;
        top: 0;
        left: 0;
    }
}