.button-group {
    display: flex;
    align-items: center;
    height: 4rem;
    border-radius: .8rem;
    overflow: hidden;

    &__item {
        color: #fff;
        font-size: 1.5rem;
        transition: all .25s ease-out;
        background-color: $background-light;
        height: 100%;
        cursor: pointer;
        @extend .flex-center;

        label{
            min-width: 11rem;
            text-align: center;
        }

        &--selected {
            font-weight: bold;
            background-color: $theme;
        }

        &--disabled {
            background-color: lighten($color: $background-dark, $amount: 1.5);
            color: $text-color-light;
        }

        &--icon-only {
            padding: 0 1.5rem;
        }
    }
}