.delivery-pair-measures {
    display: flex;

    &__image {
        width: 46%;

        &-slider {
            position: relative;
            width: 37.8rem;
            height: 35.4rem;

            .arrows {
                position: absolute;
                bottom: 1.5rem;
                right: 2.7rem;

                img {
                    &:first-child{
                        transform: rotate(180deg);
                        margin-right: .7rem;
                    }
                }
            }
        }
    }

    &__details {
        width: 54%;

        h2{
            margin-bottom: 1rem;
        }

        > .text {
            margin-bottom: 5rem;
        }
    }

    &__data {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-bottom: 6.4rem;

        li {
            border: 1px solid $border-color-light-blue;
            border-radius: .8rem;
            overflow: hidden;


            width: 16.2rem;

            > label {
                height: 3.8rem;
                font-weight: bold;
                color: $theme;
                background-color: $theme-secondary;
                display: block;
                padding: 0 2rem;
                display: flex;
                align-items: center;
            }

            div {
                padding: 2rem;

                label {                    
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-bottom: 1rem;


                    span {
                        display: inline-block;
                        height: 1.84rem;
                        color: $theme;
                        border: 1px solid;
                        border-radius: .3rem;
                        font-size: 1rem;
                        padding: 0 .75rem;
                    }
                }
            }
            
        }// DATA END        
    }

    &__frame-selection {
        h4 {
            margin-bottom: 2rem;
        }

        .text {
            margin-bottom: 2rem;
        }

        &-reference {
            height: 3.8rem;
            border-radius: .8rem;
            border: 1px solid $border-color-light-blue;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            font-size: 1.2rem;
    
            label {
                font-size: 1.2rem;
                font-weight: bold;
                color: $theme;
                background-color: $theme-secondary;
                height: 100%;
                width: 14.9rem;
                @extend .flex-center;   
                margin-right: 2rem;         
            }
    
            span {
                width: 5.7rem;
                height: 100%;
                @extend .flex-center;
                border-left: 1px solid $border-color-light-blue;
                color: $theme;
                margin-left: auto;
            }
        }    
    }

}