.download-assets {
    position: fixed;
    z-index: 90;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    color: $theme;

    height: 100%;
    width: 100%;
    background: #fff;

    &__label {
        margin-top: 2rem;
        font-size: 1.25rem;
    }

    &--complete {
        display: none;;
    }
}