@mixin kidsBubble($size) {
    &::before{
        content: '';
        width: $size;
        height: $size;
        position: absolute;
        border-radius: 50%;                
        z-index: 1;

        @extend .kids-bubble;
    }
}

@mixin kidsBackground() {
    background: var(--app-theme-main);  /* fallback for old browsers */
    background: linear-gradient(to right, var(--app-theme-dark), var(--app-theme-main)); 
}