@import './measures-container';
@import './measure-step-navigation';
@import './measure-modal';
@import './measures-data';
@import './measures-data-card';

.measures {
    height: $pageHeight;
    position: relative;
    background: #dde2e8;
    overflow: hidden;

    .lds-spinner {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
    }

    .measures-step {
        &__form {

            display: grid;
            grid-template-rows: auto;
            grid-auto-rows: auto;
            grid-gap: 1rem;
            align-items: center;
            padding: 3rem 0;

            label {
                color: $theme;
                font-weight: bold;
                font-size: 1.35rem;        
            }
        
            input{
                width: 100%; 
            }

            
            &--one {
                grid-template-columns: 5rem 1fr 1fr;
                > * {
                    flex:1;                    
                    &:first-child + input {
                        grid-column: 2 / -1;
                    }            
                    &:last-child {
                        grid-column: 2 / -1;
                    }
                }
            }

            &--two {
                grid-template-columns: 1fr 1fr;
                grid-template-rows: 3rem;

                label {
                    padding-left: .5rem;
                    font-weight: normal;

                    &:first-child, &:first-child + label {
                        align-self: flex-start;
                        font-weight: bold;
                    }
                }
            }

            &--three {
                grid-template-columns: 10rem 1fr;
            }
        }

        .button {
            margin-left: auto;
        }
    }


    &__animation {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        transition: all 1s ease-in-out;        
        transform: translateX(-12rem);

        video{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &--step1{
            transform: translateX(-35rem);
        }

        &--step2{
            transform: translateX(-38rem);
        }

        &::after {
            content: '';
            position: absolute;
            right: 0;
            top: 0;
            width: 4rem;
            height: 100%;
            background-image: linear-gradient(90deg, transparent 0%, #dde2e8 80%);
            z-index: 1;
            transform: translateX(.5rem);
        }
    }

}


