.drag-and-drop-sorter{
    &__drag {
        display: flex;

        &-item {
            flex: 1 ;
            border: 1px solid $theme;
            border-radius: 8px;
            padding:2rem;
            height:15rem;
            @extend .flex-center;
            font-size: 1.2rem;
            color: $theme;
            user-select: none;

            &:not(:last-child){
                margin-right: 1rem;
            }

            &--with-value {
                border: none;
                background: #2b86d714;
                color: transparent;
            }
        }
    }

    &__drop {
        display: flex;
        margin-bottom: 2rem;

        &-item {   
            user-select: none;
            flex: 1;
            border: 1px solid $border-color-light-blue;
            border-radius: 8px;
            padding:2rem;
            height:15rem;
            font-size: 2.5rem;
            font-weight: bold;
            color: $theme;
            position: relative;

            @extend .flex-center;

            &:not(:last-child){
                margin-right: 1rem;
            }


            &--with-value {
                background-color: $theme;
                color: #fff;
                font-weight: normal;
                font-size: 1.2rem;

                span {
                    position: absolute;
                    top: 1rem;
                    left: 1rem;
                    font-size: 1.2rem;
                    width: 2.1rem;
                    height: 2.1rem;
                    border: 1px solid #fff;
                    border-radius: .4rem;
                    @extend .flex-center;
                }
            }
        }
    }
}

