@import './product-description';
@import './product-demo';
@import './product-demo-selection';
@import './product-demo-compare';


.demonstrations {
    display: flex;
    flex-flow: column;

    &--page {
        height: $pageHeight;
    }

    &--modal {
        height: 100%;
    }


    &__canvas {
        flex: 1;
        position: relative;
        display: flex;
        align-items: flex-end;
        overflow: hidden;
    }
    
    &__controls {
        padding: 0 3.5rem;
        height: 8.1rem;
        background-color: $background-dark;
        display: flex;
        align-items: center;
        position: relative;

        .button-group{
            &:not(:last-child) {
                margin-right: 1rem;
            }
            
            &:last-child {
                margin-left: auto;
            }
        }

        &-disabled {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba($background-dark, .9);
        }
    }
}