@import './start-from';
@import './age-category';
@import './continue-with-id';
@import './home-start';

.home {
    display:grid;
    width: 100%;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 29rem 1fr;
    grid-gap: 1.5rem;

    &__start-button-wrapper {
        transition: all 1s ease-in-out;

        max-height: 10rem;
        overflow: hidden;
        opacity: 1;

        &--animate {
            max-height: 0rem;
            opacity: 0;
        }
    }
    
    &__start-button {
        &:hover,
        &:active {
            background-color: $theme;
            color: #fff;
        }
    }


    &__card {
        border: 2px solid $border-color-light-blue;
        border-radius: 15px;

        @extend .flex-center;
        flex-flow: column;

        h2{
            margin-top:1.5rem;
        }
    }

    &__main {
        grid-column: 2 / -1;
        grid-row: 1 / -1;
        border-radius: 15px;
        background-image: url('../../../assets/images/home-content-background.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: top center;

        color: #fff;
        padding: 3rem;

        @extend .flex-center;
        flex-flow: column;
        position: relative;
    }

    &__content {
        flex:1;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-flow: column;
    }

    &__eyeglass{
        width: 7rem;
        // margin-top: 8rem;
    }

    &__banner{
        max-width: 42rem;
        width: 100%;
        line-height: 1.2;
        text-align: center;
        margin-bottom: 2rem;
        // margin-bottom: 5rem;
    }

    &__start-from-id-popup{
        padding: 1rem 5rem;

        h2 {
            margin-bottom: 4rem;
            text-align: center;
        }

        div {
            display: flex;
            position: relative;

            input {
                flex: 1;
            }

            button{
                position: absolute;
                right: 0;
                height: 100%;
            }
        }
    }

    &__version {
        position: absolute;
        bottom: .75rem;
        left: 4rem;
        font-size: 1.25rem;
        color: #a0a0a0;
    }
}

/* KIDS */

.home {
    &__content{
        position: relative;
        z-index: 1;
    }
    &__main{
        &--kids{

            @include kidsBackground();

            position: relative;
            overflow: hidden;

            @include kidsBubble(calc(100vh - 10rem));

            &::before{
                bottom: -17rem;
                left: -30rem;
                opacity: .4;            
            }
        }
    }
}