.need-explorer-general-checkpoint {
    height: 100%;

    @extend .flex-center;

    &__modal {
        height: 33rem;
        width: 51rem;
        border-radius: 15px;
        @extend .box-shadow-light-blue;

        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-flow: column;
        padding: 6rem;

        margin-bottom: 15rem;

        h2 {
            color: $theme;
        }
    }

    &__modify{
        text-decoration: underline;
        color: $text-color-main;
        font-size: 1.2rem;
    }

    &__footer{
        position: absolute;
        bottom: 15rem;
        align-items: center;

        p{
            color: $text-color-sub;
            font-size: 1.17rem;
            margin: 3rem 0rem;
            width: 51rem;
            text-align: center;
        }

        div{
            display: flex;
            text-align: center;
            align-items: center;
            justify-content: center;

            button{
                margin: 0rem 1rem;
            }
        }
    }
}