.button {
    border: none;
    padding: 1.2rem 1.5rem;
    border-radius: .7rem;
    font-weight: bold;
    transition: all .25s ease-in-out;
    height: 4rem;

    display: flex;
    align-items: center;
    justify-content: space-between;
    outline: none;

    img{
        transition: all .25s ease-in-out;
    }

    // BUTTON TYPES
    &--primary {
        background-color: $theme;
        color: #fff;
        border: 1px solid $theme;
    }

    &--secondary{
        background-color: #fff;
        color: $theme;        
    }

    &--simple {
        background-color: transparent;
        border-color: #fff !important;
        color: #fff;

        &.button{
            &--active {
                background-color: #fff;
                color: $text-color-main;
            }
        }
    }

    &--disabled {
        // background: #c1c1c1;
        // color: #989898;
        // border-color: #c1c1c1 !important;

        // img{
        //     opacity: 0.35;
        // }
        opacity: .2;
    }
    // BUTTON TYPES END

    &--text {
        &-default {
            font-size: 1.5rem;
        }
        &-small {
            font-size: 1.2rem;
        }
    }

    &--icon-only {
        height: 4rem;
        width: 4rem;
        padding: 0;
        display: flex;
        justify-content: center;
        
        .button__icon {
            margin-left: 0;
        }   
    }

    &--outlined {
        border: 1px solid ;

        &.button {
            &--outline-colored {
                border-color: $theme;
            }
            &--outline-light{
                border-color: $border-color-light;
            }
        }
    }

    &--bold {
        font-weight: bold;
    }

    &--normal {
        font-weight: normal;
    }

    &--circle {
        border-radius: 50%;
    }

    &__icon, .circle-arrow{
        margin-left: 1.5rem;

        // max-width: 2rem;
        // width: 100%;
    }

    &--icon-left {
        flex-flow: row-reverse;

        img{
            margin-right: 1rem;
            margin-left: initial;
        }
    }

    &--themed {
        &.button{
            &--primary {
                background-color: var(--app-theme-main);
                color: #fff;
                border: 1px solid var(--app-theme-main);
            }

            &--secondary {
                color: var(--app-theme-main);
            }
        }
    }
}